import { CdkTable } from '@angular/cdk/table';
import { Component, HostBinding, HostListener, QueryList } from '@angular/core';
import { ColumnDefDirective } from '../../directives/table/column-def.directive';

@Component({
  selector: 'table[dg-table]',
  templateUrl: './table.component.html',
  styleUrls: ['./table.component.scss'],

  providers: [
    { provide: CdkTable, useExisting: TableComponent }
  ]
})
export class TableComponent<T> extends CdkTable<T> {
  @HostBinding('style.grid-template-columns')
  public get gridTemplateColumns(): string {
    let columnDefs = this._contentColumnDefs as QueryList<ColumnDefDirective>;

    if (columnDefs) {
      return columnDefs.map(({ sizing }) => sizing).join(' ');
    }

    return 'none';
  }

  @HostListener('window:resize')
  public updateStickyColumnStyles(): void {
    super.updateStickyColumnStyles();
  }
}
